<template>
  <view class="uni-form-item uni-column container">
    <view class="title" v-text="title"></view>
    <picker class="input" @change="bindPickerChange" range-key="text" :value="selectedIndex" :range="selectData">
      <view class="uni-input">
        <span :class="[selectData[selectedIndex].text ? '' : 'input__unselect']">{{ selectData[selectedIndex].text || placeholder }}</span>
        <image class="icon" src="@/static/icon-down.png"></image>
      </view>
    </picker>
  </view>
</template>

<script>
export default {
  name: 'PickerInput',
  props: {
    title: {
      type: String,
      default: ''
    },
    placeholder: {
      type: String,
      default: ''
    },
    selectData: {
      type: Array,
      default() {
        return [];
      }
    },
    dataType: {
      type: String,
      default: ''
    },
    dataKey: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      selectedIndex: 0
    };
  },
  methods: {
    bindPickerChange: function (e) {
      this.selectedIndex = e.detail.value;

      this.$emit('onChangeValue', {
        recordType: this.dataType,
        contentType: this.dataKey,
        value: this.selectData[this.selectedIndex].value
      });
    }
  }
};
</script>

